// components/Searchbar.tsx
"use client";
import React, { useState, useEffect, useRef } from "react";

interface SearchbarProps {
  initialTerm?: string;
  onSearch?: (term: string) => void;
}

const Searchbar: React.FC<SearchbarProps> = ({
  initialTerm = "",
  onSearch,
}) => {
  const [searchTerm, setSearchTerm] = useState(initialTerm);
  const [isSearchPage, setIsSearchPage] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    // ÿßŸÑÿ™ÿ≠ŸÇŸÇ ŸÖŸÜ ÿßŸÑŸÖÿ≥ÿßÿ± ÿßŸÑÿ≠ÿßŸÑŸä ÿπŸÜÿØ ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖŸÉŸàŸÜ
    const pathname = window.location.pathname;
    setIsSearchPage(pathname === "/search");

    // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ŸáŸÜÿßŸÉ initialTermÿå ÿπŸäŸÜŸá
    if (initialTerm) {
      setSearchTerm(initialTerm);
    }
  }, [initialTerm]);

  // ÿ™ÿ±ŸÉŸäÿ≤ ÿ™ŸÑŸÇÿßÿ¶Ÿä ÿπŸÜÿØ ŸÅÿ™ÿ≠ ÿµŸÅÿ≠ÿ© ÿßŸÑÿ®ÿ≠ÿ´
  useEffect(() => {
    if (isSearchPage && inputRef.current) {
      setTimeout(() => {
        if (inputRef.current) {
          inputRef.current.focus();
          // Ÿàÿ∂ÿπ ÿßŸÑŸÖÿ§ÿ¥ÿ± ŸÅŸä ŸÜŸáÿßŸäÿ© ÿßŸÑŸÜÿµ
          if (searchTerm) {
            inputRef.current.setSelectionRange(
              searchTerm.length,
              searchTerm.length
            );
          }
        }
      }, 100);
    }
  }, [isSearchPage, searchTerm]);

  const handleFocus = () => {
    if (!isSearchPage) {
      // ÿ•ÿ∞ÿß ŸÉŸÜÿß ŸÅŸä ÿßŸÑÿµŸÅÿ≠ÿ© ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©ÿå ÿßŸÜŸÇŸÑ ÿ•ŸÑŸâ ÿµŸÅÿ≠ÿ© ÿßŸÑÿ®ÿ≠ÿ´
      if (searchTerm.trim()) {
        window.location.href = `/search?term=${encodeURIComponent(
          searchTerm.trim()
        )}`;
      } else {
        window.location.href = "/search";
      }
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && searchTerm.trim()) {
      if (isSearchPage) {
        // ÿ•ÿ∞ÿß ŸÉŸÜÿß ŸÅŸä ÿµŸÅÿ≠ÿ© ÿßŸÑÿ®ÿ≠ÿ´ÿå ÿßÿ≥ÿ™ÿÆÿØŸÖ onSearch
        if (onSearch) {
          onSearch(searchTerm);
        }
      } else {
        // ÿ•ÿ∞ÿß ŸÉŸÜÿß ŸÅŸä ÿßŸÑÿµŸÅÿ≠ÿ© ÿßŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©ÿå ÿßŸÜÿ™ŸÇŸÑ ÿ•ŸÑŸâ ÿµŸÅÿ≠ÿ© ÿßŸÑÿ®ÿ≠ÿ´
        window.location.href = `/search?term=${encodeURIComponent(
          searchTerm.trim()
        )}`;
      }
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchTerm(value);

    // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ŸÅŸä ÿµŸÅÿ≠ÿ© ÿßŸÑÿ®ÿ≠ÿ´ ŸàŸÜÿ±ŸäÿØ ÿ®ÿ≠ÿ´ ŸÅŸä ÿßŸÑŸàŸÇÿ™ ÿßŸÑÿ≠ŸÇŸäŸÇŸä (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä)
    // if (isSearchPage && onSearch) {
    //   onSearch(value);
    // }
  };

  return (
    <div>
      <input
        ref={inputRef}
        type="text"
        value={searchTerm}
        onChange={handleChange}
        onFocus={handleFocus}
        onKeyPress={handleKeyPress}
        readOnly={!isSearchPage}
        className="searchbar focus:placeholder-transparent
 border w-full px-1 py-1 text-sm h-9 border-gray-600 rounded-xl text-center text-white focus:!bg-[#232432] focus:!border-[#7a7af0] focus:!outline-none"
        placeholder="Search through over 70 million podcasts and episodes..."
      />
    </div>
  );
};

export default Searchbar;
